* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Calibri, 'Trebuchet MS', Sans-Serif;
    color: darkred;
}

/* Menu */

nav {
    text-align: center;
    background-color: antiquewhite;
    padding: 5px;
    margin: 5px;
}

nav li {
    display: inline;
}

nav li a.opcion-enlace {
    margin-right: 40px;
    text-decoration: none;
}

a {
    color: darkred;
    font-style: italic;
}

a:hover {
    color: #f5c45b;
}

img.destacada {
    margin-left: 5%;
    padding: 5px;
    height: 50px;
    width: 50px;
}

.opcion-detalle {
    margin-left: 40px;
    padding-top: 10px;
    font-size: 1.5em;
}


/* Pie de Página */

footer {
    background-color: lightyellow;
    border-top: 1px solid darkred;
    padding-top: 5px;
    padding-bottom: 5px;
    color: darkred;
    font-size: 0.6em;
    text-align: center;
}

/* Secciones */

section.categoria {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 30%;
    margin-right: 30%;
}

section.cuerpo {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 15%;
    margin-right: 15%;
}

section.cuerpo p {
	margin-top: 10px;
}

section.cuerpo ul {
	margin-left: 30px;
}


/* Configuración Cajas */

.contenedor {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow:hidden;
}

.grupo-izquierda {
    float: left;
}

.grupo-derecha {
    float: right;
    text-align: right;
}


/* Elementos Comunes */

p.texto-titulo-cabecera {
    font-size: 140%;
    text-align: center;
    font-style: italic;
}

h1 {
    font-size: 120%;
    padding-top: 10px;
    padding-bottom: 10px;
}

h2 {
    font-size: 100%;
	padding-top: 10px;
}

li {
    font-size: 90%;	
}

p {
    font-size: 90%;
}

p.texto-imagen {
    margin-top: 0%;
    font-size: 70%;
    text-align: center;
    font-style: italic;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 60%;
    max-height: 250px;
}

img.logo {
    height: 80px;
    width: auto;
    padding: 10px;
}

img.logo-pie {
    height: 40px;
    width: auto;
    clip-path: polygon(26% 10%, 60% 10%, 60% 0%, 89% 47%, 60% 100%, 60% 90%, 26% 89%);
}

img.fondo {
	opacity: 0.1;
    padding-top: 150px;
    padding-bottom: 150px;
}

button {
    background-color: darkred;
    color: white;
    border-radius: 8px;
    width: 120px;
    height: 25px;
    font-size: 80%;
}

span.fuente {
    font-weight: bold;
}

#detalleFuente {
	visibility: hidden;
	margin-top: 5px;
	margin-left: 30px;
	font-style: italic;
    font-size: 80%;
}

/* Animaciones */

.mov-rebote {
    animation-name: rebote;
    animation-duration: 4s;
}

@keyframes rebote {
  0% {
    transform: scale(0.1);
    opacity: 0;
  }
  60% {
    transform: scale(1.4);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}

.mov-pulso {
    animation: [animation-name] [animation-duration] [animation-iteration-count];
    animation: pulso 5s infinite;    
}

@keyframes pulso {
  0% {
    transform: scale(0.1);
    opacity: 0;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0.1);
    opacity: 0;
  }
}

.mov-giro {
    animation-name: giro;
    animation-duration: 4s;
}

@keyframes giro {
  0% {
    transform: rotate(0deg);
    opacity: 0;
  }
  100% {
    transform: rotate(360deg);
    opacity: 1;
  }
}


/* Estructura de la página en función del tamaño de la pantalla */

.contenedor-flexible {
    margin-left: auto;
    margin-right: auto;
    overflow:hidden;
    display: flex;
    flex-wrap: wrap;
}

.contenedor-flexible-peque {
    width: 450px;
    margin-left: auto;
    margin-right: auto;
    overflow:hidden;
    display: flex;
    flex-wrap: wrap;
}


.grupo-unico {
    padding: 5px;
    width: 100%;
}

.grupo-dostercios-unico {
    padding: 5px;
    width: 66%;
    float: left;
}

.grupo-tercio-unico {
    padding: 5px;
    width: 33%;
    float: left;
}

.grupo-mitad-unico {
    padding: 5px;
    width: 50%;
    float: left;
}
